* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.form_container {
  display: flex;
  justify-content: center;
  margin-top: 150px;
}
.form_container #form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 20%;
}
.form_container #form input {
  padding: 12px 8px;
}
.form_container #form input:focus {
  box-shadow: 0 3px 4px 1px #000;
  transform: scale(1.05);
  border-radius: 5px;
  outline: none;
}
.form_container #form .action {
  padding: 12px 8px;
  text-transform: capitalize;
  font-size: 1.2rem;
  background-color: #fff;
  color: #000;
  letter-spacing: 1px;
  cursor: pointer;
  border: 1px solid;
  transition: 0.2s ease-in-out;
}
.form_container #form .action:hover {
  box-shadow: 0 3px 4px 1px #000;
  transform: scale(1.05);
  border-radius: 5px;
}